@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular");
}
@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.markdown-body {
  margin-top: 32px;
  text-align: left;
}
.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3);
}

/* font chart */
.c-size-s {
  font-size: 12px;
}
.c-size-m {
  font-size: 14px;
}
.c-size-l {
  font-size: 16px;
}
.c-normal {
  font-family: Mont;
}
.c-bold {
  font-family: MontBold;
  font-weight: 800;
}

/* position-type */
.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

/* transition effect */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}
.move-leave-active {
  opacity: 0;
}
.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}

/* end */
input {
  /* input placeholder style */
}
input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999;
}
input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999;
}
input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999;
}
input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999;
}
.input-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
}
.input-wrapper .input__icon {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto auto;
    height: 1em;
    color: #c0c4cc;
}
.input-wrapper.have-prefix-icon .input-inner {
    padding-left: 24px;
}
.input-wrapper.have-prefix-icon .input__icon:first-child {
    left: 6px;
}
.input-wrapper.have-suffix-icon .input-inner {
    padding-right: 24px;
}
.input-wrapper.have-suffix-icon .input__icon:last-child {
    right: 6px;
}
.input-wrapper .input-inner,
  .input-wrapper .textarea-inner {
    padding: 0 12px;
    width: 100%;
    min-height: 32px;
    line-height: 32px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.65);
    text-overflow: ellipsis;
    outline: none;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
.input-wrapper .input-inner:focus,
    .input-wrapper .textarea-inner:focus {
      text-overflow: ellipsis;
      border: 1px solid #209cee;
      -webkit-box-shadow: 0 0 5px 0 rgba(32, 156, 238, 0.5);
              box-shadow: 0 0 5px 0 rgba(32, 156, 238, 0.5);
}
.input-wrapper .input-inner:disabled,
    .input-wrapper .textarea-inner:disabled {
      background-color: rgba(192, 196, 204, 0.25);
      color: #c0c4cc;
      cursor: not-allowed;
}
.input-wrapper .input-inner.have-prepand,
    .input-wrapper .textarea-inner.have-prepand {
      border-radius: 0 4px 4px 0;
}
.input-wrapper .input-inner.have-prepand.have-append,
      .input-wrapper .textarea-inner.have-prepand.have-append {
        border-radius: 0 0 0 0;
}
.input-wrapper .input-inner.have-append,
    .input-wrapper .textarea-inner.have-append {
      border-radius: 4px 0 0 4px;
}
.input-wrapper .textarea-inner {
    min-height: 2.5em;
    line-height: 32px;
}
