@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular");
}
@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.markdown-body {
  margin-top: 32px;
  text-align: left;
}
.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3);
}

/* font chart */
.c-size-s {
  font-size: 12px;
}
.c-size-m {
  font-size: 14px;
}
.c-size-l {
  font-size: 16px;
}
.c-normal {
  font-family: Mont;
}
.c-bold {
  font-family: MontBold;
  font-weight: 800;
}

/* position-type */
.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

/* transition effect */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}
.move-leave-active {
  opacity: 0;
}
.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}

/* end */
input {
  /* input placeholder style */
}
input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999;
}
input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999;
}
input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999;
}
input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999;
}
.select-wrapper {
  position: relative;
  padding: 0 24px 0 12px;
  width: 100%;
  min-height: 32px;
  line-height: 32px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.65);
  text-overflow: ellipsis;
  outline: none;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.select-wrapper:focus {
    text-overflow: ellipsis;
    border: 1px solid #209cee;
    -webkit-box-shadow: 0 0 5px 0 rgba(32, 156, 238, 0.5);
            box-shadow: 0 0 5px 0 rgba(32, 156, 238, 0.5);
}
.select-wrapper.is-disabled {
    background-color: rgba(192, 196, 204, 0.25);
    color: #c0c4cc;
    cursor: not-allowed;
}
.select-wrapper.is-disabled:focus {
      -webkit-box-shadow: none;
              box-shadow: none;
      border: 1px solid #ccc;
}
.select-wrapper .placeholder {
    color: #999;
}
.select-wrapper .select__item_tag {
    position: relative;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 2px 6px 2px 0;
    padding: 4px 4px;
    height: 1em;
    color: #909399;
    background: #f0f2f5;
    border-radius: 4px;
}
.select-wrapper .select__item_tag .delete-btn {
      margin-left: 4px;
      border-radius: 50%;
      color: #fff;
      background: #c0c4cc;
      cursor: pointer;
}
.select-wrapper .arrow {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin: auto auto;
    display: inline-block;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 14px;
    height: 14px;
}
.select-wrapper .select-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: 12px;
    padding: 4px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    min-width: 100%;
    max-height: 170px;
    overflow: auto;
    z-index: 2;
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
}
