@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular"); }

@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga'; }

.markdown-body {
  margin-top: 32px;
  text-align: left; }

.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content; }

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd; }

::-webkit-scrollbar-track {
  background-color: #fff; }

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3); }

/* font chart */

.c-size-s {
  font-size: 12px; }

.c-size-m {
  font-size: 14px; }

.c-size-l {
  font-size: 16px; }

.c-normal {
  font-family: Mont; }

.c-bold {
  font-family: MontBold;
  font-weight: 800; }

/* position-type */

.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto; }

/* transition effect */

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s; }

.fade-enter,
.fade-leave-to {
  opacity: 0; }

.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s; }

.move-leave-active {
  opacity: 0; }

.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0; }

/* end */

input {
  /* input placeholder style */ }

input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999; }

input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999; }

input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999; }

input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999; }

.breadcrumb-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.breadcrumb-wrapper .breadcrumb__separator {
    display: inline-block;
    margin: 0 8px;
    font-weight: 700;
    color: #c0c4cc;
    font-size: 0.8em; }

.breadcrumb-wrapper .breadcrumb__item {
    color: #1f2f3d;
    text-decoration: none; }

.breadcrumb-wrapper .breadcrumb__item:hover {
      color: #209cee;
      cursor: pointer; }

.breadcrumb-wrapper .breadcrumb__item.router-link-active {
      font-weight: 600; }
