@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular");
}
@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.markdown-body {
  margin-top: 32px;
  text-align: left;
}
.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3);
}

/* font chart */
.c-size-s {
  font-size: 12px;
}
.c-size-m {
  font-size: 14px;
}
.c-size-l {
  font-size: 16px;
}
.c-normal {
  font-family: Mont;
}
.c-bold {
  font-family: MontBold;
  font-weight: 800;
}

/* position-type */
.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

/* transition effect */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}
.move-leave-active {
  opacity: 0;
}
.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}

/* end */
input {
  /* input placeholder style */
}
input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999;
}
input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999;
}
input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999;
}
input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999;
}
.tabs-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.tabs-wrapper .tabs__header {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    z-index: 2;
}
.tabs-wrapper .tabs__header .tabs__header-item {
      min-width: 72px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
}
.tabs-wrapper .tabs__header .tabs__header-item.is-active {
        color: #209cee;
}
.tabs-wrapper .tabs__header .tabs__header-item.is-active::after {
          display: block;
          content: "";
          width: 100%;
          height: 2px;
          background: #209cee;
}
.tabs-wrapper .tabs__header .tabs__header-item.is-disabled {
        cursor: not-allowed;
        color: #c0c4cc;
}
.tabs-wrapper .tabs__header .tabs__header-item:not(.is-disabled):not(.is-active)::after {
        display: block;
        content: "";
        width: 100%;
        height: 2px;
        background: #209cee;
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
}
.tabs-wrapper .tabs__header .tabs__header-item:not(.is-disabled):not(.is-active):hover {
        color: #209cee;
}
.tabs-wrapper .tabs__header .tabs__header-item:not(.is-disabled):not(.is-active):hover::after {
          display: block;
          content: "";
          width: 100%;
          background: #209cee;
          -webkit-transform: scaleX(1);
                  transform: scaleX(1);
}
.tabs-wrapper .tabs__header .tabs__header-item:not(:last-child) {
        margin-right: 32px;
}
.tabs-wrapper .tabs__header.is-card {
      border-bottom: none;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        line-height: 1em;
        margin: 0 0;
        border-top: 1px solid #e4e7ed;
        border-right: 1px solid #e4e7ed;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item:first-child {
          border-left: 1px solid #e4e7ed;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item.is-active {
          border-bottom: 2px solid transparent;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item.is-active::after {
            display: none;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item:not(.is-disabled):not(.is-active) {
          border-bottom: 1px solid #e4e7ed;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item:not(.is-disabled):not(.is-active)::after {
            display: none;
}
.tabs-wrapper .tabs__header.is-card .tabs__header-item:not(.is-disabled):not(.is-active):hover::after {
            display: none;
}
.tabs-wrapper .tabs__header.is-card .delete-btn {
        display: inline-block;
}
.tabs-wrapper .tabs__header .delete-btn {
      display: none;
}
.tabs-wrapper .tabs__content {
    padding: 32px 0;
}
