@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular");
}
@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.markdown-body {
  margin-top: 32px;
  text-align: left;
}
.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3);
}

/* font chart */
.c-size-s {
  font-size: 12px;
}
.c-size-m {
  font-size: 14px;
}
.c-size-l {
  font-size: 16px;
}
.c-normal {
  font-family: Mont;
}
.c-bold {
  font-family: MontBold;
  font-weight: 800;
}

/* position-type */
.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

/* transition effect */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}
.move-leave-active {
  opacity: 0;
}
.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}

/* end */
input {
  /* input placeholder style */
}
input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999;
}
input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999;
}
input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999;
}
input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999;
}
.slider-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 32px;
}
.slider-wrapper .tooltip {
    position: absolute;
    bottom: 100%;
    display: none;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 30px;
    height: 30px;
    font-size: 12px;
    color: #fff;
    background: rgba(0, 0, 0, 0.7);
}
.slider-wrapper .progress {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto auto;
    border-radius: 3px;
    height: 6px;
    background: #409eff;
    z-index: 1;
}
.slider-wrapper .slider-inner {
    position: relative;
    margin: 0 0;
    padding: 0 0;
    width: 100%;
    height: 100%;
    overflow: visible;
    -webkit-appearance: none;
}
.slider-wrapper .slider-inner:active {
      cursor: -webkit-grabbing;
      cursor: grabbing;
}
.slider-wrapper .slider-inner:active + .tooltip {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
.slider-wrapper .slider-inner::-webkit-slider-thumb {
      -webkit-appearance: none;
}
.slider-wrapper .slider-inner:focus {
      outline: none;
}
.slider-wrapper .slider-inner::-ms-track {
      width: 100%;
      cursor: pointer;
      background: transparent;
      /* Hides the slider so custom styles can be added */
      border-color: transparent;
      color: transparent;
}
.slider-wrapper .slider-inner::-webkit-slider-thumb {
      margin-top: -6px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      position: relative;
      z-index: 3;
      border: 2px solid #409eff;
      background: #fff;
      cursor: -webkit-grab;
      cursor: grab;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.slider-wrapper .slider-inner::-webkit-slider-thumb:active {
        cursor: -webkit-grabbing;
        cursor: grabbing;
}
.slider-wrapper .slider-inner.is-disabled::-webkit-slider-thumb {
      border: 2px solid #c4c4c4;
      cursor: not-allowed;
}
.slider-wrapper .slider-inner::-moz-range-thumb {
      width: 18px;
      height: 18px;
      border-radius: 50%;
      position: relative;
      z-index: 3;
      border: 2px solid #409eff;
      background: #fff;
      cursor: grab;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
      transform: translateZ(1px);
}
.slider-wrapper .slider-inner::-moz-range-thumb:active {
        cursor: grabbing;
}
.slider-wrapper .slider-inner.is-disabled::-moz-range-thumb {
      border: 2px solid #c4c4c4;
}
.slider-wrapper .slider-inner::-ms-thumb {
      width: 18px;
      height: 18px;
      border-radius: 50%;
      position: relative;
      z-index: 3;
      border: 2px solid #409eff;
      background: #fff;
      cursor: grab;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.slider-wrapper .slider-inner::-ms-thumb:active {
        cursor: grabbing;
}
.slider-wrapper .slider-inner.is-disabled::-ms-thumb {
      border: 2px solid #c4c4c4;
}
.slider-wrapper .slider-inner::-webkit-slider-runnable-track {
      width: 100%;
      height: 6px;
      background: #e4e7ed;
      border-radius: 3px;
      cursor: pointer;
}
.slider-wrapper .slider-inner::-moz-range-track {
      width: 100%;
      height: 6px;
      background: #e4e7ed;
      border-radius: 3px;
      cursor: pointer;
}
.slider-wrapper .slider-inner::-ms-track {
      width: 100%;
      height: 6px;
      cursor: pointer;
      background: transparent;
      border-color: transparent;
      border-width: 16px 0;
      color: transparent;
}
.slider-wrapper .slider-inner::-ms-fill-lower {
      background: #409eff;
      border-radius: 3px;
}
.slider-wrapper .slider-inner::-ms-fill-upper {
      background: #c4c4c4;
      border-radius: 3px;
}
.slider-wrapper .slider-inner.is-disabled::-webkit-slider-runnable-track {
      cursor: not-allowed;
}
.slider-wrapper .slider-inner.is-disabled::-moz-range-track {
      cursor: not-allowed;
}
.slider-wrapper .slider-inner.is-disabled::-ms-track {
      cursor: not-allowed;
}
.slider-wrapper.is-disabled .slider-inner {
    cursor: not-allowed;
}
.slider-wrapper.is-disabled::-webkit-slider-thumb {
    border: 2px solid #c4c4c4;
    cursor: not-allowed;
}
