@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular");
}
@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.markdown-body {
  margin-top: 32px;
  text-align: left;
}
.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3);
}

/* font chart */
.c-size-s {
  font-size: 12px;
}
.c-size-m {
  font-size: 14px;
}
.c-size-l {
  font-size: 16px;
}
.c-normal {
  font-family: Mont;
}
.c-bold {
  font-family: MontBold;
  font-weight: 800;
}

/* position-type */
.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

/* transition effect */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}
.move-leave-active {
  opacity: 0;
}
.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}

/* end */
input {
  /* input placeholder style */
}
input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999;
}
input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999;
}
input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999;
}
input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999;
}
.page-wrapper {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #fafafa;
}
.page-wrapper .header-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    height: 30px;
    padding: 0 15px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: #444b57;
    background: #fff;
}
.page-wrapper .header-wrapper .logo {
      width: 100px;
      height: 20px;
      background: url(/static/img/logo.png);
      background-size: cover;
}
.page-wrapper .header-wrapper .btns {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: space-evenly;
          -ms-flex-pack: space-evenly;
              justify-content: space-evenly;
      width: 200px;
}
.page-wrapper .header-wrapper .btns .link {
        text-decoration: none;
}
.page-wrapper .nav-sidebar-wrapper {
    position: fixed;
    top: 45px;
    left: 15px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 240px;
    height: 90%;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
}
.page-wrapper .nav-sidebar-wrapper:hover {
      overflow: auto;
}
.page-wrapper .nav-sidebar-wrapper .nav-sidebar-subitem,
    .page-wrapper .nav-sidebar-wrapper .nav-sidebar-item {
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      width: 100%;
      padding: 0 32px;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      min-height: 40px;
      text-decoration: none;
      color: #444b57;
}
.page-wrapper .nav-sidebar-wrapper .nav-sidebar-subitem.router-link-exact-active,
      .page-wrapper .nav-sidebar-wrapper .nav-sidebar-item.router-link-exact-active {
        position: relative;
        color: #4d94e6;
        background: #f1f8ff;
}
.page-wrapper .nav-sidebar-wrapper .nav-sidebar-subitem.router-link-exact-active::before,
        .page-wrapper .nav-sidebar-wrapper .nav-sidebar-item.router-link-exact-active::before {
          position: absolute;
          top: 0;
          left: 0;
          width: 2px;
          height: 100%;
          content: "";
          background: #4ca0fa;
}
.page-wrapper .nav-sidebar-wrapper .nav-sidebar-subitem.is-hint,
      .page-wrapper .nav-sidebar-wrapper .nav-sidebar-item.is-hint {
        color: #a5a5a5;
}
.page-wrapper .nav-sidebar-wrapper .nav-sidebar-item:first-child:not(.is-hint):hover,
    .page-wrapper .nav-sidebar-wrapper .nav-sidebar-subitem:not(.is-hint):hover {
      color: #4d94e6;
      background: #f1f8ff;
}
.page-wrapper .nav-sidebar-wrapper .nav-sidebar-subitem {
      padding: 0 48px;
}
.page-wrapper .content-wrapper {
    position: absolute;
    top: 30px;
    left: 286px;
    right: 36px;
    bottom: 0;
    margin: 16px 0;
    padding: 64px 64px;
    background: #fff;
    border-radius: 4px;
    z-index: 1;
    overflow: auto;
}
