@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local("material-design-icons"), local("MaterialIcons-Regular");
}
@font-face {
  font-family: 'MontBold';
  src: url("/static/fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Mont';
  src: url("/static/fonts/montserrat-regular-webfont.woff2") format("woff2"), url("/static/fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.markdown-body {
  margin-top: 32px;
  text-align: left;
}
.markdown-body pre {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #ddd;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(144, 147, 153, 0.3);
}

/* font chart */
.c-size-s {
  font-size: 12px;
}
.c-size-m {
  font-size: 14px;
}
.c-size-l {
  font-size: 16px;
}
.c-normal {
  font-family: Mont;
}
.c-bold {
  font-family: MontBold;
  font-weight: 800;
}

/* position-type */
.p-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}

/* transition effect */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s;
  transition: opacity 0.3s, transform 0.3s, left 0.3s, right 0.3s, top 0.3s, bottom 0.3s, -webkit-transform 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.move-enter-active {
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}
.move-leave-active {
  opacity: 0;
}
.move-enter {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}

/* end */
input {
  /* input placeholder style */
}
input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #999;
}
input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #999;
}
input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #999;
}
input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #999;
}
.hover-tip-wrapper {
  position: relative;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.hover-tip-wrapper .hover-tip__part {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.hover-tip-wrapper .hover-tip__tip {
    display: none;
    padding: 20px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    white-space: normal;
    word-break: break-all;
    border-radius: 4px;
    background-color: #fff;
    border-color: transparent #fff transparent transparent;
    -webkit-box-shadow: 0 0 4px 0 #babec1;
            box-shadow: 0 0 4px 0 #babec1;
    z-index: 2;
}
.hover-tip-wrapper .hover-tip__tip .arrow {
      position: absolute;
      width: 10px;
      height: 10px;
      background-color: #fff;
      -webkit-box-shadow: 0 0 4px 0 #babec1;
              box-shadow: 0 0 4px 0 #babec1;
}
.hover-tip-wrapper .hover-tip__tip .block {
      position: absolute;
      background: #fff;
}
.hover-tip-wrapper .hover-tip__part:hover + .hover-tip__tip {
    display: block;
}
.hover-tip-wrapper .left-center,
  .hover-tip-wrapper .right-center {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.hover-tip-wrapper .left-center .arrow,
    .hover-tip-wrapper .right-center .arrow {
      top: 50%;
}
.hover-tip-wrapper .left-center .block,
    .hover-tip-wrapper .right-center .block {
      top: 50%;
      width: 8px;
      height: 15px;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
}
.hover-tip-wrapper .right-center {
    margin-left: 8px;
    left: 100%;
}
.hover-tip-wrapper .right-center .arrow {
      left: 0;
      -webkit-transform: translateY(-50%) translateX(-50%) rotateZ(45deg);
              transform: translateY(-50%) translateX(-50%) rotateZ(45deg);
}
.hover-tip-wrapper .right-center .block {
      left: 0;
}
.hover-tip-wrapper .left-center {
    margin-right: 8px;
    right: 100%;
}
.hover-tip-wrapper .left-center .arrow {
      right: 0;
      -webkit-transform: translateY(-50%) translateX(50%) rotateZ(45deg);
              transform: translateY(-50%) translateX(50%) rotateZ(45deg);
}
.hover-tip-wrapper .left-center .block {
      right: 0;
}
.hover-tip-wrapper .top-center,
  .hover-tip-wrapper .bottom-center {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.hover-tip-wrapper .top-center .arrow,
    .hover-tip-wrapper .bottom-center .arrow {
      left: 50%;
}
.hover-tip-wrapper .top-center .block,
    .hover-tip-wrapper .bottom-center .block {
      left: 50%;
      width: 15px;
      height: 8px;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}
.hover-tip-wrapper .bottom-center {
    margin-top: 8px;
    top: 100%;
}
.hover-tip-wrapper .bottom-center .arrow {
      top: 0;
      -webkit-transform: translateY(-50%) translateX(-50%) rotateZ(45deg);
              transform: translateY(-50%) translateX(-50%) rotateZ(45deg);
}
.hover-tip-wrapper .bottom-center .block {
      top: 0;
}
.hover-tip-wrapper .top-center {
    margin-bottom: 8px;
    bottom: 100%;
}
.hover-tip-wrapper .top-center .arrow {
      bottom: 0;
      -webkit-transform: translateY(50%) translateX(-50%) rotateZ(45deg);
              transform: translateY(50%) translateX(-50%) rotateZ(45deg);
}
.hover-tip-wrapper .top-center .block {
      bottom: 0;
}
